const BaseRest = require('../rest.js');

export default class creatTaskList extends BaseRest {
    async indexAction() {
        if (this.isPost) {
            const PasswordList = think.mongo('TaskList', 'mongoPassword');
            let param = this.post();
            console.log(param);
            delete param.accessToken;
            let result = await PasswordList.where({
                uid: param.uid
            }).thenAdd(param);
            console.log(result);
        }
    }
}